* {
  box-sizing: border-box;
}

.dummy-data-container {
  background-color: black;
  min-height: 100vh;
  color: #ffffff;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dummy-data-heading {
  font-family: "Roboto";
  font-weight: bold;
}
.dummy-data-table,
th {
  border: 1px #ffffff solid;
}
th {
  padding: 10px;
  border-left: none;
  border-right: none;
}
.profile {
  height: 50px;
  width: 40px;
}

td {
  text-align: center;
}
